.circle-bg {
  position: relative;
}
.circle-bg:before {
  content: "";
  width: 120%;
  padding-bottom: 120%;
  border-radius: 1000rem;
  position: absolute;
  background: #f2f5f7;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
/*Sizes*/
.circle-bg-xs:before {
  width: 25%;
  padding-bottom: 25%;
}
.circle-bg-sm:before {
  width: 50%;
  padding-bottom: 50%;
}
.circle-bg-lg:before {
  width: 200%;
  padding-bottom: 200%;
}
.circle-bg-xl:before {
  width: 400%;
  padding-bottom: 400%;
}
/*Positions (center). Uses -c sufix*/
.circle-bg-top-c:before {
  top: 0;
}
.circle-bg-right-c:before {
  right: -50%;
  left: inherit;
}
.circle-bg-left-c:before {
  left: 0;
  transform: translate(-50%, -50%);
}
.circle-bg-bottom-c:before {
  transform: translate(-50%, 50%);
  bottom: 0;
  top: inherit;
}
/*Positions (box edge)*/
.circle-bg-top:before {
  transform: translate(-50%, -5%);
  top: 0;
}
.circle-bg-right:before {
  transform: translate(5%, -50%);
  left: inherit;
  right: 0;
}
.circle-bg-left:before {
  left: 0;
  transform: translate(-5%, -50%);
}
.circle-bg-bottom:before {
  top: inherit;
  bottom: 0;
  transform: translate(-50%, 5%);
}
/*Combinations*/
.circle-bg-top.circle-bg-left:before {
  transform: translate(-5%, -5%);
}
.circle-bg-top.circle-bg-right:before {
  transform: translate(5%, -5%);
}
.circle-bg-bottom.circle-bg-left:before {
  transform: translate(-5%, 5%);
}
.circle-bg-bottom.circle-bg-right:before {
  transform: translate(5%, 5%);
}
/*Not simetric center for big headings*/
.circle-bg-center:before {
  transform: translate(-35%, 0%);
  left: 0;
}
.circle-bg-clip {
  overflow: hidden;
}
.circle-bg-light-blue:before {
  background: #dbf0fd;
}
.circle-bg-blue:before {
  background: #2e4369;
}
.circle-bg-navy:before {
  background: #253655;
}
