@flag-info: url(data:image/svg+xml;base64,);
@flag-default: url(data:image/svg+xml;base64,);

@logo-full-primary: url(data:image/svg+xml;base64,);
@logo-full-inverse: url(data:image/svg+xml;base64,);

@logo-text-primary: url(data:image/svg+xml;base64,);
@logo-text-inverse: url(data:image/svg+xml;base64,);


.logo,
.logo-primary,
.navbar .navbar-brand  {
	display: block;
	background-image: @logo-full-primary;
	background-repeat: no-repeat;
	background-position: 50% 50%;
}

.logo-inverse,
.navbar-inverse .navbar-brand, .affix .navbar-brand  {
	background-image: @logo-full-inverse;
}

.logo-text,
.logo-text-primary {
	background-image: @logo-text-primary;
	background-repeat: no-repeat;
	background-position: 50% 50%;
}
.logo-text-inverse {
	background-image: @logo-text-inverse;
}

.flag {
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-image: @flag-default;

	&.flag-info {
		background-image: @flag-info;
	}
}

.logo-3 {
  height: 24px;
  width: 138px;
}
.logo-4 {
  height: 32px;
  width: 184px;
}
.logo-5 {
  height: 40px;
  width: 231px;
}
