
.loader {
	position: relative;
	display: inline-block;
	height: 160px;
	width: 160px;

	.loader-flag  {
		top: 58px;
		left: 52px;
		position: relative;
		height: 52px;
		width: 52px;
	}
	.loader-flag-stroke {
		animation: loaderStroke 3.5s infinite;
	}
	.loader-flag-fill {
		display: block;
		position: absolute;
		top: 1px;
		opacity: 1;
		animation: loaderFill 3.5s infinite;
	}
	.loader-spinner {
		width: 160px;
		height: 160px;
		position: absolute;
		background-image: url(data:image/svg+xml;base64,);
		animation: rotating 1.4s linear infinite;
	}
}

/* Animations */
@keyframes loaderStroke {
  30% {
    stroke-dashoffset: 300;
    opacity: 0;
  }
  40% {
    stroke-dashoffset: 300;
    opacity: 1;
  }
  70% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
  100% {
    stroke-dashoffset: 0;
    opacity: 0;
  }
}

@keyframes loaderFill {
  0% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
  70% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
